.sv-demo-hd {
  padding-left: 16px;
  color: rgba(0, 0, 0, 0.75);
}
.sv-demo-hd .sv-demo-title {
  font-size: 32px;
  font-weight: 400;
  margin: 27px 0px 15px;
  display: flex;
  align-items: center;
}
.sv-demo-hd .sv-demo-title img {
  width: 36px;
  height: 36px;
  margin-right: 16px;
}
.sv-demo-hd .sv-demo-subtitle {
  font-size: 13px;
  font-weight: 400;
  margin: 0px 0px 27px;
}
.sv-demo-list {
  background-color: #ffffff;
  margin: 0px 16px 12px;
  border-radius: 2px;
}
.sv-demo-list .sv-list-header {
  position: relative;
  height: 70px;
  line-height: 70px;
  color: #404040;
  font-size: 16px;
  padding: 0px 0px 0px 24px;
  display: flex;
}
.sv-demo-list .sv-demo-category-name {
  flex: 1;
}
.sv-demo-list .sv-demo-category-arrow {
  width: 50px;
  display: flex;
  justify-content: center;
}
.sv-demo-list .sv-demo-category-arrow span {
  display: flex;
  align-items: center;
  transform: rotate(90deg);
  transition: transform 0.3s;
}
.sv-demo-list .sv-demo-category-arrow span.active {
  transform: rotate(-90deg);
  transition: transform 0.3s;
}
.sv-demo-list .sv-demo-category-arrow img {
  height: 15px;
  width: 15px;
}
.sv-list-body > div:not(:last-child) > div {
  border-bottom: 1px solid #ddd;
}
.sv-list-body .sv-list-item {
  padding-left: 24px;
  font-size: 14px;
}
.sv-list-body .sv-list-line {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #666;
  padding-right: 15px;
}
.sv-list-body .sv-list-content {
  line-height: 1.5;
  width: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-top: 7px;
  padding-bottom: 7px;
}
.sv-list-body .sv-list-arrow {
  display: block;
  width: 15px;
  height: 15px;
  margin-left: 8px;
  background-image: url(data:image/png;base64,);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
