::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track {
  background: #efefef;
  border-radius: 2px;
}
::-webkit-scrollbar-thumb {
  background: #bfbfbf;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #333;
}
::-webkit-scrollbar-corner {
  background: #179a16;
}
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
.app {
  display: flex;
  flex-direction: column;
  background: #f5f5f9;
}
.nav-bar {
  height: 70px;
}
.nav-bar > div {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  padding-top: 10px;
  background: #3f3f3f;
  z-index: 10;
}
.nav-bar img {
  margin: 2px 0;
  width: 100%;
}
.nav-bar .nav-bar-date {
  position: absolute;
  top: 10px;
  right: 50%;
  margin-right: -14px;
  font-size: 14px;
  color: #fff;
}
.nav-bar .nav-bar-url {
  height: 40px;
  padding: 0 10px;
}
.nav-bar .nav-bar-url > div {
  font-size: 14px;
  height: 28px;
  line-height: 28px;
  color: #fff;
  background-color: #a2a2a2;
  margin: 0 auto;
  border-radius: 4px;
  white-space: nowrap;
  overflow-x: scroll;
}
.nav-bar .nav-bar-url > div::-webkit-scrollbar {
  width: 0px;
  height: 0px;
}
.nav-demo-name {
  font-size: 19px;
  color: #404040;
  padding: 21px 0 21px 15px;
  text-align: left;
  height: 24px;
  line-height: 24px;
  display: flex;
  align-items: center;
}
.nav-demo-name img {
  width: 20px;
  height: 20px;
}
.nav-demo-name i {
  font-style: normal;
  margin: 0 10px;
  font-size: 15px;
}
.demo-blank {
  height: 20px;
}
.demo-title {
  font-size: 14px;
  padding: 30px 12px 5px;
  color: rgba(69, 90, 100, 0.6);
  background: #f8f8f8;
}
.demo-container {
  background: #fff;
  margin: 5px;
  padding: 10px;
}
.demo-example-margin {
  margin: 8px 8px 0 0;
  display: inline-block;
}
.demo-btn-container {
  padding: 10px 24px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
