@keyframes svSpinMove {
  from {
    opacity: 0.3;
  }
  to {
    opacity: 1;
  }
}
.sv-loading {
  display: inline-block;
  font-size: 14px;
  color: #1989fa;
}
.sv-loading span {
  display: inline-block;
  vertical-align: middle;
}
.sv-loading-icon {
  position: relative;
}
.sv-spin-spot {
  position: relative;
  animation: sv-rotate 1.2s infinite linear;
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
}
.sv-spin-spot-item {
  position: absolute;
  display: block;
  width: 0.6em;
  height: 0.6em;
  background-color: #1989fa;
  border-radius: 100%;
  transform: scale(0.75);
  transform-origin: 50% 50%;
  opacity: 0.3;
  animation: svSpinMove 1s infinite linear alternate;
}
.sv-spin-spot-item:nth-child(1) {
  top: 0;
  left: 0;
}
.sv-spin-spot-item:nth-child(2) {
  top: 0;
  right: 0;
  animation-delay: 0.4s;
}
.sv-spin-spot-item:nth-child(3) {
  right: 0;
  bottom: 0;
  animation-delay: 0.8s;
}
.sv-spin-spot-item:nth-child(4) {
  bottom: 0;
  left: 0;
  animation-delay: 1.2s;
}
@keyframes sv-circular {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -40;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -120;
  }
}
.sv-loading-spiner {
  position: relative;
  display: inline-block;
  width: 2em;
  max-width: 100%;
  height: 2em;
  max-height: 100%;
  vertical-align: middle;
  animation: sv-rotate 0.8s linear infinite;
  animation-duration: 2s;
}
.sv-loading__circular {
  display: block;
  width: 100%;
  height: 100%;
}
.sv-loading__circular circle {
  animation: sv-circular 1.5s ease-in-out infinite;
  stroke: currentColor;
  stroke-width: 3;
  stroke-linecap: round;
}
.sv-loading-vertical {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.sv-loading-vertical .sv-loading-message {
  margin-top: 8px;
  margin-left: 0;
}
.sv-loading-message {
  margin-left: 8px;
  color: inherit;
}
