.sv-button {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  height: 44px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 42px;
  text-align: center;
  border-radius: 2px;
  -webkit-appearance: none;
  -webkit-text-size-adjust: 100%;
}
.sv-button::before {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background-color: #000;
  border: inherit;
  border-color: #000;
  border-radius: inherit;
  /* inherit parent's border radius */
  transform: translate(-50%, -50%);
  opacity: 0;
  content: ' ';
}
.sv-button:active::before {
  opacity: 0.1;
}
.sv-button-default {
  color: #323233;
  background-color: #fff;
  border: 1px solid #ebedf0;
}
.sv-button-primary {
  color: #fff;
  background-color: #07c160;
  border: 1px solid #07c160;
}
.sv-button-info {
  color: #fff;
  background-color: #1989fa;
  border: 1px solid #1989fa;
}
.sv-button-danger {
  color: #fff;
  background-color: #f00;
  border: 1px solid #f00;
}
.sv-button-warning {
  color: #fff;
  background-color: #ff976a;
  border: 1px solid #ff976a;
}
.sv-button-large {
  width: 100%;
  height: 50px;
  line-height: 48px;
}
.sv-button-normal {
  padding: 0 15px;
  font-size: 14px;
}
.sv-button-small {
  min-width: 60px;
  height: 30px;
  padding: 0 8px;
  font-size: 12px;
  line-height: 28px;
}
.sv-button__loading {
  display: inline-block;
  vertical-align: top;
}
.sv-button-mini {
  display: inline-block;
  min-width: 50px;
  height: 22px;
  font-size: 10px;
  line-height: 20px;
}
.sv-button-mini + .sv-button-mini {
  margin-left: 5px;
}
.sv-button-plain {
  background-color: #fff;
}
.sv-button-plain.sv-button-primary {
  color: #07c160;
}
.sv-button-plain.sv-button-info {
  color: #1989fa;
}
.sv-button-plain.sv-button-danger {
  color: #f00;
}
.sv-button-plain.sv-button-warning {
  color: #ff976a;
}
.sv-button-hairline {
  border-width: 0;
}
.sv-button-hairline::after {
  position: absolute;
  box-sizing: border-box;
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 1px solid #ebedf0;
  transform: scale(0.5);
  border-color: inherit;
  border-radius: 4px;
}
.sv-button-hairline.sv-button-round::after {
  border-radius: 10em;
}
.sv-button-hairline.sv-button-square::after {
  border-radius: 0;
}
.sv-button-round {
  border-radius: 10em;
}
.sv-button-square {
  border-radius: 0;
}
.sv-button [class*='sv-icon'] {
  line-height: inherit;
  vertical-align: top;
}
.sv-button__icon + .sv-button-text,
.sv-button__loading + .sv-button-text {
  display: inline-block;
  margin-left: 5px;
  vertical-align: top;
}
