.sv-notify {
  box-sizing: border-box;
  padding: 8px 16px;
  font-size: 14px;
  line-height: 20px;
  white-space: pre-wrap;
  text-align: center;
  word-wrap: break-word;
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 100;
