.sv-switch {
  position: relative;
  display: inline-block;
  box-sizing: content-box;
  width: 2em;
  height: 1em;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 1em;
  transition: background-color 0.3s;
}
.sv-switch-node {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 1em;
  height: 1em;
  background-color: #fff;
  border-radius: 100%;
  box-shadow: 0 3px 1px 0 rgba(0, 0, 0, 0.05), 0 2px 2px 0 rgba(0, 0, 0, 0.1), 0 3px 3px 0 rgba(0, 0, 0, 0.05);
  transition: 0.3s;
}
.sv-switch-loading {
  top: 25%;
  left: 25%;
  width: 50%;
  height: 50%;
  line-height: 1;
}
.sv-switch-on {
  background-color: #1989fa;
}
.sv-switch-on .sv-switch-node {
  transform: translateX(1em);
}
.sv-switch-disabled {
  opacity: 0.4;
}
