/*
gaojingweo 2019/9
*/
.sv-tab-wrap {
  position: relative;
  padding-top: 44px;
}
.sv-tab-container {
  height: 44px;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  position: absolute;
}
.sv-tab-scrollable {
  overflow-y: hidden;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 17px;
  box-sizing: content-box;
}
.sv-tab-scrollable::-webkit-scrollbar {
  width: 0px;
  height: 0px;
}
.sv-tab-self {
  display: flex;
  background-color: #fff;
  height: 44px;
  position: relative;
}
.sv-tab-self .sv-tab-item.sv-tab-selected {
  color: #1989fa;
  border-bottom: 3px solid #1989fa;
}
.sv-tab-self .sv-tab-bar-inner {
  display: block;
  background-color: #1989fa;
  margin: auto;
  height: 100%;
  transition: width 0.3s cubic-bezier(0.35, 0, 0.25, 1);
}
.sv-tab-self .sv-tab-item {
  display: block;
  flex: 1;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background: linear-gradient(180deg, #e5e5e5, #e5e5e5, rgba(229, 229, 229, 0)) bottom left no-repeat;
  background-size: 100% 1px;
  font-size: 14px;
  text-align: center;
  line-height: 44px;
  color: #666;
}
.sv-tab-self .sv-tab-item.sv-tab-disabled {
  color: #ddd;
}
.sv-tab-scrollable .sv-tab-item {
  flex: 0 0 22%;
}
.sv-tab-scrollable .sv-tab-ink-bar {
  bottom: 17px;
  position: absolute;
}
.sv-tab-bar-transition-backward {
  transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1) 0.09s, left 0.3s cubic-bezier(0.35, 0, 0.25, 1);
}
.sv-tab-bar-transition-forward {
  transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1), left 0.3s cubic-bezier(0.35, 0, 0.25, 1) 0.09s;
}
.sv-tab-ink-bar {
  position: absolute;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #1989fa;
  text-align: center;
}
