.sv-write {
  background: #fff;
}
.sv-write-container {
  padding: 10px 15px;
  position: relative;
  line-height: 24px;
  display: flex;
  font-size: 14px;
}
.sv-write-required::after {
  position: absolute;
  content: '*';
