.sv-stepper {
  display: flex;
}
.sv-stepper-btn {
  position: relative;
  width: 32px;
  height: 32px;
  background-color: #f9fafb;
  border-radius: 2px;
}
.sv-stepper-btn:active {
  background-color: #eff6fd;
}
.sv-stepper-reduce::before,
.sv-stepper-add:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  background: #111a34;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 13px;
  height: 1px;
}
.sv-stepper-add::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  background: #111a34;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 1px;
  height: 13px;
}
.sv-stepper-write {
  background-color: #f9fafb;
  margin: 0 5px;
  text-align: center;
  flex: 1;
}
.sv-stepper-btn-disabled {
  opacity: 0.5;
}
