.demo-icon .demo-icon-container {
  background: #fff;
  margin: 5px;
}
.demo-icon ul {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  width: 300px;
}
.demo-icon li {
  height: 100px;
  width: 100px;
  line-height: 100px;
  display: flex;
  flex-direction: column;
}
.demo-icon li:hover .sv-iconfont {
  font-size: 48px;
  transition: all 0.3s;
}
.demo-icon .sv-iconfont {
  font-size: 32px;
  line-height: 64px;
  color: #3f3f3f;
  transition: all 0.3s;
}
.demo-icon span {
  font-size: 16px;
  line-height: 36px;
  color: #666;
}
